import { EyeOutlined } from '@ant-design/icons';
import { Button, Form, Input } from 'antd';
import { login } from 'apis/modules/Auth';
import IconEyeClose from 'assets/Icon/IconEyeClose';
import Routers from 'constants/routers';
import { CookieKeys } from 'constants/services';
import noti from 'libs/notification';
import { ILoginForm } from 'models/login';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { setCookie } from 'utils/cookies';

const LoginForm = () => {
  const { t } = useTranslation();
  const [form] = Form.useForm();
  const navi = useNavigate();
  const [isProcess, setIsProcess] = useState(false);

  const onFinish = async (value: ILoginForm) => {
    try {
      setIsProcess(true);
      const payload = value;
      const response = (await login(payload)) as any;
      if (response && response.code === 'SUCCESS') {
        const { data } = response;
        setCookie(CookieKeys.Token, data.token);
        noti('success', 'Login successfully');
        navi(Routers.Jobs);
      } else {
        noti('error', response.message);
      }
    } catch (error: any) {
      noti('error', error.message);
    } finally {
      setIsProcess(false);
    }
  };

  return (
    <div className="px-6 p py-16 login__form">
      <h3 className="text-[34px] font-medium">{t('page.login.title')}</h3>
      <Form layout="vertical" form={form} className="md:w-[400px] w-auto mt-6" autoComplete="off" onFinish={onFinish}>
        <Form.Item
          label="Email"
          name="email"
          rules={[
            { required: true, message: '' },
            () => ({
              validator(_: any, value: any) {
                if (!value) {
                  return Promise.reject(new Error(t('page.login.message.emailRequire')));
                }
                if (!value.match(/\S+@\S+\.\S+/)) {
                  return Promise.reject(new Error(t('page.login.message.emailInvalid')));
                }
                return Promise.resolve();
              }
            })
          ]}
        >
          <Input className="h-9" />
        </Form.Item>
        <Form.Item label="Password" name="password" rules={[{ required: true }]}>
          <Input.Password
            className="h-9"
            iconRender={(visible: boolean) => (!visible ? <IconEyeClose /> : <EyeOutlined width={14} height={14} />)}
          />
        </Form.Item>
        <Form.Item>
          <Button
            disabled={isProcess}
            danger
            type="primary"
            className="w-full h-9 mt-4 rounded bg-red"
            htmlType="submit"
          >
            {t('page.login.label.loginButton')}
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default LoginForm;
