import LoginImage from 'assets/images/login_image.png';
import LoginForm from './components/LoginForm';
import { getCookie } from 'utils/cookies';
import { CookieKeys } from 'constants/services';
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
const LoginPage = () => {
  const navi = useNavigate();
  const isLogin = () => {
    return getCookie(CookieKeys.Token);
  };

  useEffect(() => {
    if (isLogin()) navi(-1);
  }, [navi]);
  return (
    <div className="login h-full w-full flex items-center justify-center">
      <div className="login__container md:py-7 md:px-[38px]">
        <div className="flex md:gap-20 gap-6 md:items-center flex-col md:flex-row w-full">
          <img
            src={LoginImage}
            alt="Login"
            className="w-auto md:w-full lg:h-[650px] xl:h-full h-[400px] object-contain "
          />
          <LoginForm />
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
